               slickMini: {
              slidesToShow: 1,
        slidesToScroll: 1,
        dots: false,
        arrows: true,
        prevArrow: '<span class="right"><i class="fal fa-angle-left"></i></span>',
        nextArrow: '<span class="left"><i class="fal fa-angle-right"></i></span>',
        autoplay: true,
        autoplaySpeed: 2000,
                    },
     SlickComparison:{
       slidesToShow: 2,
        slidesToScroll: 1,
        dots: false,
        arrows: true,
        prevArrow: '<span class="right-round"><i class="fal fa-angle-left"></i></span>',
        nextArrow: '<span class="left-round"><i class="fal fa-angle-right"></i></span>',
        autoplay: true,
        autoplaySpeed: 2000,
        responsive: [
            {
                breakpoint: 1024,
                settings: {
                    slidesToShow: 1,
                    slidesToScroll: 1,
                    infinite: true,
                    dots: false
                }
    },
            {
