<template>

<div class="review-form">
    <div class="container">



   <form @submit.prevent="Review">
        <!--rv-form area-->
        <div class="rv-form">
            <div class="row">
                <div class="col-md-8">
                    <div class="rev-inputs px-3">
                        <div class="row">
                            <div class="col-md-12">
                                <p class="rv-tt">

                                    You have chosen to review {{this.$route.params.id}}
                                </p>
                            </div>
                        </div>
                        <div class="row">
                             
                            <div class="col-md-2 align-self-center" v-if="Productdetails">
                               <img  :src="'/images/productimages/'+Productdetails.featureimage" alt="not" class="w-100">
                            </div>
                            <div class="col-md-10 align-self-center">
                                <h3>How would you rate {{this.$route.params.id}}(on scale of 5) <span class="text-danger">*</span></h3>
                                <p>
                                   <star-rating v-model="form.rate"></star-rating>
                                </p>
                            </div>
                        </div>
                        <div class="row">
                            <div class="col-md-12">
                              
                                    <div class="form-group">
                                        <label for="reviewtitle" class="d-block">Review Title <span class="text-danger">*</span> <span class="float-right" style="font-size: 13px; color:#666666;">( Maximum 20 words )</span></label>
                                        <input type="text" v-model="form.title" class="form-control" id="reviewtitle" placeholder="Review Title">
                                    </div>

                                    <div class="form-group">
                                        <label for="userreview" class="d-block">Your Review <span class="text-danger">*</span><span class="float-right" style="font-size: 13px; color:#666666;">( Minimum 50 characters )</span></label>
                                        <textarea class="form-control" v-model="form.review" id="userreview" rows="3"></textarea>
                                    </div>
                               
                            </div>
                        </div>
                        <div class="row">
                            <div class="col-md-6">
                        


                                    <div class="form-group">
                                        <label for="pros" class="d-block">Pros <span class="float-right" style="font-size: 13px; color:#666666;">( Maximum 100 words )</span></label>
                                        <textarea class="form-control" v-model="form.pros" id="pros" rows="3"></textarea>
                                    </div>
                            
                            </div>
                            <div class="col-md-6">
                                <form>

                                    <div class="form-group">
                                        <label for="cons" class="d-block">Cons <span class="float-right" style="font-size: 13px; color:#666666;">( Maximum 100 words )</span></label>

                                        <textarea class="form-control" v-model="form.cons" id="cons" rows="3"></textarea>
                                    </div>
                                </form>
                            </div>
                        </div>
                        <div class="row mt-5">
                            <div class="col-md-12">
                                <h3 class="issue-hd mb-0">
                                    Which issue did you face with {{this.$route.params.id}} ?
                                </h3>
                            </div>
                        </div>
                        <div class="row pb-4 pt-3">
                            <div class="col-md-4">
                                <div class="form-check">
                                    <input class="form-check-input" type="checkbox" v-model="form.issue" value="first" id="defaultCheck1">
                                    <label class="form-check-label" for="defaultCheck1">
                                        Default checkbox
                                    </label>
                                </div>
                            </div>
                            <div class="col-md-4">
                                <div class="form-check">
                                    <input class="form-check-input" v-model="form.issue" type="checkbox" value="second" id="defaultCheck2">
                                    <label class="form-check-label" for="defaultCheck2">
                                        Default checkbox
                                    </label>
                                </div>
                            </div>
                            <div class="col-md-4">
                                <div class="form-check">
                                    <input class="form-check-input" v-model="form.issue" type="checkbox" value="third" id="defaultCheck3">
                                    <label class="form-check-label" for="defaultCheck3">
                                        Default checkbox
                                    </label>
                                </div>
                            </div>

                        </div>
                        <div class="row pt-3">
                            <div class="col-md-12 text-center">
                                <button type="submit" class="btn rev-btn">Submit your review</button>
                            </div>
                        </div>
                    </div>

                </div>
                <div class="col-md-4">
                    <div class="notes p-3">
                        <h3>
                            <i class="far fa-comment-alt"></i> What makes a good review?
                        </h3>

                        <ul class="p-3 mb-0">
                            <li>Lorem ipsum dolor sit amet, consectetur adipisicing.Lorem ipsum dolor sit amet, consectetur adipisicing.</li>
                            <li>Lorem ipsum dolor sit amet, consectetur adipisicing.Lorem ipsum dolor sit amet, consectetur adipisicing.</li>
                            <li>Lorem ipsum dolor sit amet, consectetur adipisicing.Lorem ipsum dolor sit amet, consectetur adipisicing.</li>
                            <li>Lorem ipsum dolor sit amet, consectetur adipisicing.Lorem ipsum dolor sit amet, consectetur adipisicing.</li>
                        </ul>
                    </div>
                </div>
            </div>


        </div>
        <!--rv-form area end-->
 </form>






    </div>
</div>


              
</template>


<script>
import StarRating from 'vue-star-rating'
export default {
  name: "Userreview",
  components: {
  StarRating
},

  data() {
    return {
       form: new Form({
        product_id:'',
        rate:1,
        title:"",
        review: "",
        pros: "",
        cons: "",
        issue: [],
        }),
      authenticatedname: null,
      authenticatedimage: null,
      Productdetails: null
    };
  },

  created() {
    document.title = this.$route.params.id;
  },
  mounted() {
 axios.get(`/product/${this.$route.params.id}`).then(response => {
          this.Productdetails = response.data.productdetails;
     this.form.product_id=response.data.productdetails.id
    });
      var user = localStorage.getItem("user");
    if (user) {
      this.authenticatedimage = JSON.parse(
        localStorage.getItem("user")
      ).userimage;
      this.authenticatedname = JSON.parse(
        localStorage.getItem("user")
      ).username;
    }
  },

  methods: {
    logOutNow() {
      //alert(5);
      localStorage.clear();
      this.$router.push("/login");
    },
   Review(){
         if(this.authenticatedname==null){   alert('Please Login First');
       }
       else{
        this.$eventBus.$emit("loadingStatus", true);
        this.form.post("user/userreview")
        //console.log('ok');
         .then(({ response}) => {
          this.$eventBus.$emit("loadingStatus", false);
          [toastr.success("Your review submit succefully")],
            [this.$router.push(`/product/${this.$route.params.id}`)];
                    })
      
        .catch(function(response) {
          console.log(response);
          toastr.warning("Sorry Try Agin");
        });
   }
   }
  }
};
</script>

  <style>
span img {
  max-width: 600px;
}
</style>
  


