import Vue from 'vue'
import Router from 'vue-router'
//en area start
import Index from '@/components/Home/En/Index.vue'
import Blogpostdetails from '@/components/Home/En/Blogpostdetails.vue'
import Pagedetails from '@/components/Home/En/Blogpagedetails.vue'
import Productdetails from '@/components/Home/En/Productdetails.vue'
import Userreview from '@/components/Home/En/Userreview.vue'
import UserreviewDetails from '@/components/Home/En/Userreviewdetails.vue'
import UserRegister from '@/components/Home/En/Userregister.vue'
import UserLogin from '@/components/Home/En/Userlogin.vue'
import UserDashboard from '@/components/Home/En/Userdashboard.vue'
import Showroom from '@/components/Home/En/Showroom.vue'    //for showroom view
import Showroomdetails from '@/components/Home/En/Showroomdetails.vue'  //fore showroom location view
import BrandWishShowroom from '@/components/Home/En/brandwiseshowroom.vue'  //for Brandwish Showroom location view
import BrandDetails from '@/components/Home/En/braddetails.vue'  //for Branddetails view
import BlogAll from '@/components/Home/En/Blog.vue'  //for All Blog view
//en area end

//Bn area start

import BNIndex from '@/components/Home/Bn/Index.vue'
//import BNBlogpostdetails from './components/Home/Bn/Blogpostdetails.vue'
//import BNPagedetails from './components/Home/Bn/Blogpagedetails.vue'
// import UserRegister from './components/Home/Bn/Userregister.vue'
// import UserLogin from './components/Home/Bn/Userlogin.vue'
import BNUserDashboard from '@/components/Home/Bn/Userdashboard.vue'
//Bn area end
Vue.use(Router)
export default new Router({

  routes: [
    
    {
      path: '/',
      name: 'Index',
      component: Index
    },
    { path: '/login', name: 'UserLogin', component: UserLogin },
    { path: '/register', component: UserRegister },
    {
        path: '/',
        name: 'UserDashboard',
        component: UserDashboard,
        children: [
            { path: 'blog/:id', component: Blogpostdetails },
            { path: 'page/:id', component: Pagedetails },
            { path: 'product/:id', component: Productdetails },
            {path: 'userreview/:id', component: Userreview },
            {path: 'userreviewdetails/:id', component: UserreviewDetails },
            { path: 'Showroom', component: Showroom }, //for showroom view
            { path: 'showroom/:id', component: Showroomdetails }, //for showroom location view
            { path: 'brandwiseshowroom/:id', component: BrandWishShowroom }, //for showroom location view
            { path: 'brand/:id', component: BrandDetails }, //for Brand details view
            { path: 'blog', component: BlogAll }, //for All Blog

            //profile area end
          
            //blog page  area start

            //blog page  area end

        ],
      },

      { path: '/bn', component: BNIndex },
    {

        path: '/bn',
        name: 'BNDashboard',
        component: BNUserDashboard,
        children: [
            //{ path: 'blog/:id', component: BNBlogpostdetails },
            //{ path: 'page/:id', component: BNPagedetails },
          

        ],


    },
  ],
    mode: 'history', //for remove # in url 
 
})



