<template>
<section id="user-review">

    <div class="container">
        <div class="product-contant mt-4">
            <div class="row">
                <div class="col-md-12">
                    <div class="row mr-0 ml-0" style="border-bottom:1px solid #ddd">
                        <div class="col-md-6 align-self-center">

                            <h3 class=" p-3 mb-0">
                               {{reviewDetails.title}}  {{reviewDetails.cc}}
                            </h3>

                        </div>
                        <div>
                         
                              <router-link :to="`/product/${reviewDetails.slug}`" class="col-md-6 align-self-center text-right"> <i class="fas fa-caret-left"></i> Product Details</router-link>
                        </div>
                    </div>


                    <div class="row mr-0 ml-0" style="border-bottom:1px solid #ddd">
                        <div class="col-md-2" style="border-right:1px solid #ddd">
                            <h1 class="p-3">Rating</h1>
                        </div>
                        <div class="col-md-5" style="border-right:1px solid #ddd">
                            <h1 class="p-3">progress</h1>
                        </div>
                        <div class="col-md-5">
