                                <div class="col-md-6 align-self-center">
                                    <p class="mb-0">

                                        Filter By Source
                                    </p>
                                </div>
                                <div class="col-md-6 align-self-center">
                                    <select style="width:100%;">
                                        <option value="" disabled="" selected="">Select One</option>
                                        <option>Daraz</option>

                                    </select>
                                </div>
                            </div>

                        </div>
                    </div>



                    <div class="row mr-0 ml-0" style="border-bottom:1px solid #ddd" v-for="(userreview,index) in reviewDetails.userreview">
                        <div class="col-md-2">
                            <div class="review-content p-3">
                                <p class="mb-0">
                                        <span v-for="rev in userreview.rate"><i class="far fa-star text-warning" ></i></span>
                                    
                                </p>
                                <p class="mb-0">{{userreview.created_at}},</p>
                                <p class="mb-0">By {{userreview.user.firstname}} {{userreview.user.lastname}}</p>
                            </div>
                        </div>
                        <div class="col-md-10" style="border-left:1px solid #ddd">
                            <h2>{{userreview.title}}</h2>
                            <p>
                            {{userreview.review}}
                            </p>
                        </div>
                    </div>








                </div>
            </div>







        </div>
    </div>



</section>

              
