      authenticatedimage: null,
      reviewDetails: [],
      UserReview:[],
    };
  },

  created() {
    document.title = this.$route.params.id;
  },
  mounted() {
 axios.get(`/userreviewdetails/${this.$route.params.id}`).then(response => {
          this.reviewDetails = response.data.reviewdetails;
      this.UserReview = response.data.reviewdetails.userreview;
    });
      var user = localStorage.getItem("user");
    if (user) {
      this.authenticatedimage = JSON.parse(
        localStorage.getItem("user")
      ).userimage;
      this.authenticatedname = JSON.parse(
        localStorage.getItem("user")
      ).username;
    }
  },
computed: {
  totalReview: function(UserReview){
      let sum =this.UserReview.length;
      return sum;
   } ,
  
    OverallUserRationresult: function(UserReview){
       
      let sum = 0;
      for(let i = 0; i < (this.UserReview).length; i++){
        sum +=this.UserReview[i].rate;
      }

     return (sum/(this.UserReview.length)).toFixed(1);
   },
//       OnestarReview: function(Review){
//        let sum1=0;
//        let sum2=0;
//        let sum3=0;
//        let sum4=0;
//        let sum5=0;
      
//       for(let i = 0; i < (this.Review).length; i++){
//           if(this.Review[i].rate==1){
//        sum1 +=1;
//         }
//          if(this.Review[i].rate==2){
//          sum2 +=1;
//         } 
//         if(this.Review[i].rate==3){
//        sum3 +=1;
//         }
//          if(this.Review[i].rate==4){
//          sum4 +=1;
//         }
//          if(this.Review[i].rate==5){
//         sum5 +=1;
//         }
//       }
//  return sum1,sum2,sum3,sum4,sum5;
 
//    }
},
  methods: {
    logOutNow() {
      //alert(5);
      localStorage.clear();
      this.$router.push("/login");
    },
   Review(){
         if(this.authenticatedname==null){   alert('Please Login First');
       }
       else{
        this.$eventBus.$emit("loadingStatus", true);
        this.form.post("user/userreview")
        //console.log('ok');
         .then(({ response}) => {
          this.$eventBus.$emit("loadingStatus", false);
          [toastr.success("Your review submit succefully")],
            [this.$router.push(`/product/${this.$route.params.id}`)];
                    })
      
        .catch(function(response) {
          console.log(response);
          toastr.warning("Sorry Try Agin");
        });
   }
   }
  }
};
</script>

  <style>
span img {
  max-width: 600px;
}
</style>
  


