// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import Slick from 'vue-slick';
import JQuery from 'jquery';
window.$ = JQuery;
import { filter} from './filter';
import VueFacebook from 'vue-facebook';
import vueHeadful from 'vue-simple-headful';
import VueToastr from "vue-toastr";

Vue.prototype.$eventBus = new Vue(); // Global event bus
//import v form
import {
  Form,
  HasError,
  AlertError
} from 'vform'
Vue.component(HasError.name, HasError)
Vue.component(AlertError.name, AlertError)
window.Form = Form
//import v form end
window.axios = require('axios');
window.toastr = require('toastr')
 window.axios.defaults.baseURL ='https://beta.bikebd.com';
// window.axios.defaults.headers.common['X-Requested-With'] = 'XMLHttpRequest';
Vue.config.productionTip = false
Vue.use(Slick,VueFacebook,vueHeadful,VueToastr);
/* eslint-disable no-new */
Vue.component('pagination', require('laravel-vue-pagination'));
//for sent all page token
router.beforeEach((to, from, next) => {
  window.scrollTo(0, 0); //for scroll
  window.axios.defaults.headers.common['Authorization'] = 'Bearer ' + localStorage.getItem('token');
  //window.superadmin =  localStorage.getItem( 'superadmininfo'); 
  next()
});

new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



