    </div>
    <!--File  uplpad Modal-->
        <div class="modal fade" id="Offerapply">
            <div class="modal-dialog">
                <div class="modal-content">
                    <div class="modal-header">
                        <h4 class="modal-title">You have Offer !!! </h4>
                        <button type="button" @click="FormReset()" class="close" data-dismiss="modal"
                            aria-label="Close">
                            <span aria-hidden="true">&times;</span></button>
                            <hr>
                           
                    </div>
                    <div class="modal-body">
                         <p v-if="OfferSetting">{{OfferSetting.offerinfo}}</p>
                         <p v-if="OfferSetting" class="text-danger">{{OfferSetting.content}}</p>
                         <p v-if="OfferSetting" class="text-info text-center">{{OfferSetting.winerinfo}}</p>
                       <form  @submit.prevent="OfferApply">
                            <label for="name">Your Full Name *</label>
                     <input  v-model="offerform.name" type="text" required  class="form-control" placeholder="Enter your full name" :class="{ 'is-invalid': offerform.errors.has('name') }"  id="name" >
                        <has-error :form="offerform" field="name"></has-error>
                          <label for="mobile">Your Mobile *</label>
                     <input  v-model="offerform.mobile" type="number" required class="form-control" maxlength="11" placeholder="Enter your Mobile no (11 digits only)" :class="{ 'is-invalid': offerform.errors.has('mobile') }"  id="name" >
                        <has-error :form="offerform" field="mobile"></has-error>
                 <label for="metadescription">Full Address *</label>
                    <textarea v-model="offerform.address" class="form-control" rows="2" required placeholder="Enter your full address" :class="{ 'is-invalid': offerform.errors.has('address') }"></textarea>
                  <has-error :form="offerform" field="address"></has-error>
                   <label for="location">Location Name *</label>
                   <select  id="location" v-model="offerform.location" class="form-control" required>
                        <option disabled value="">Select One</option>
                      <option v-for="dist in District" :value="dist.districtname" >{{dist.districtname}}</option>
                     
                    </select>
                     <has-error :form="offerform" field="location" ></has-error>

                  
                     <label for="age">Your Age *</label>
                   <select  id="age"  class="form-control" v-model="offerform.age" required>
                        <option disabled value="">Select One</option>
                        <option value="16-20 Year">18-24 Year</option>
                        <option value="25-34 Year">25-34 Year</option>
                        <option value="35-50 Year">35-50 Year</option>
                        <option value="50-60 Year">50-60 Year</option>
                        <option value="60+">360+</option>
                             </select>
                     <has-error :form="offerform" field="age" ></has-error>
                     <label for="profession">Your Profesion *</label>
                   <select  id="profession"  class="form-control" v-model="offerform.profession" required>
                        <option disabled value="">Select One</option>
                        <option value="Job">Job</option>
                        <option value="Business">Business</option>
                        <option value="Student">Student</option>
                        <option value="Freelencer">Freelencer</option>
                        <option value="Unemployed">Unemployed</option>
                        <option value="Self Employed">Self Employed</option>
                       </select>
                     <has-error :form="offerform" field="profession" ></has-error>
                     
                     <hr/>
                        <button type="submit" class="btn btn-success text-right">Submit</button> 
                          <button type="button" @click="FormReset()" class="btn btn-warning" data-dismiss="modal"
                            aria-label="Close">
                            <span aria-hidden="true">Cancel</span></button>

                        </form>
                         </div>
                    </div>
                </div>
                <!-- /.modal-content -->
            </div>
            <!-- /.modal-dialog -->
    
   <!--File upload Modal end-->
</section>
              
</template>


<script>
import Slick from 'vue-slick';
export default {
    name: "Productdetails",
  components: { Slick },
  data() {
    return {
              SlickTopCompetitors:{
         slidesToShow: 3,
        slidesToScroll: 1,
        dots: false,
        arrows: true,
        prevArrow: '<span class="tc-right"><i class="fal fa-angle-right"></i></span>',
        nextArrow: '<span class="tc-left"><i class="fal fa-angle-left"></i></span>',
        autoplay: true,
        autoplaySpeed: 2000,
        responsive: [
            {
                breakpoint: 1024,
                settings: {
                    slidesToShow: 3,
                    slidesToScroll: 3,
                    infinite: true,
                    dots: false
                }
    },
            {
                breakpoint: 600,
                settings: {
                    slidesToShow: 2,
                    slidesToScroll: 2
                }
    },
            {
                breakpoint: 480,
                settings: {
                    slidesToShow: 1,
                    slidesToScroll: 1
                }
    }
  ]

        
    },

    SlickRecentProduct:{
         slidesToShow: 3,
        slidesToScroll: 1,
        dots: false,
        arrows: true,
        prevArrow: '<span class="rv-right"><i class="fal fa-angle-right"></i></span>',
        nextArrow: '<span class="rv-left"><i class="fal fa-angle-left"></i></span>',
        autoplay: true,
        autoplaySpeed: 2000,
        responsive: [
            {
                breakpoint: 1024,
                settings: {
                    slidesToShow: 3,
                    slidesToScroll: 3,
                    infinite: true,
                    dots: false
                }
    },
            {
                breakpoint: 600,
                settings: {
                    slidesToShow: 2,
                    slidesToScroll: 2
                }
    },
            {
                breakpoint: 480,
                settings: {
                    slidesToShow: 1,
                    slidesToScroll: 1
                }
    }
  ]
    },
      questionform: new Form({
        id:'',
        question: ""
      }),
      offerform: new Form({
        name: "",
        location: "",
        mobile: "",
        address: "",
        age: "",
        profession: "",
        
      }),

      headful: {
        description: "",
        keywords: ""
      },
      authenticatedname: null,
      authenticatedimage: null,
      title: null,
      name: null,
      description: null,
      blogpostdetails: [],
      Allrelatedblog: [],
      allProductdetails: [],
     Review:[],
     TopCompetitors:[],
     RecentProduct:[],
     Recentblog:[],
     CommingProduct:[],
     PopularBike:[],
     District:[],
     OfferSetting:[],
    };
  },

   created () {
     document.title = this.$route.params.id;
           axios.get(`/product/${this.$route.params.id}`)
              .then(response => {
                        this.headful.description=response.data.productdetails.shortdescription;
                        // this.headful.description=response.data.productdetails.metadescription;
                    this.headful.keywords=response.data.productdetails.keyword;
                     this.questionform.id = response.data.productdetails.id;
                    this.allProductdetails = response.data.productdetails;
                     this.Review = response.data.productdetails.userreview;
                  });


       

      },
  mounted() {
       var user = localStorage.getItem("user");

    if (user) {
      this.authenticatedimage = JSON.parse(
        localStorage.getItem("user")
      ).userimage;
      this.authenticatedname = JSON.parse(
        localStorage.getItem("user")
      ).username;
    }

    axios.get('/productlist')
    .then(response => {
     this.TopCompetitors = response.data.feature;
    this.RecentProduct = response.data.recentproduct;
      this.CommingProduct = response.data.upcommingproduct;
      this.PopularBike = response.data.upcommingproduct;
    });
    
         
  },
computed: {
  totalReview: function(Review){
      let sum =this.Review.length;
      return sum;
   } ,
   DisplacementColor:function(){
       return this.allProductdetails.displacement/200
   },
   MileageColor:function(){
       return this.allProductdetails.mileage/2000
   },
   RmColor:function(){
       return this.allProductdetails.rm/20000
   },
   TorquenmColor:function(){
       return this.allProductdetails.torquenm/2000
   },
    OverallUserRationresult: function(Review){
       
      let sum = 0;
      for(let i = 0; i < (this.Review).length; i++){
        sum +=this.Review[i].rate;
      }

     return (sum/(this.Review.length)).toFixed(1);
   },
      OnestarReview: function(Review){
       let sum1=0;
       let sum2=0;
       let sum3=0;
       let sum4=0;
       let sum5=0;
      
      for(let i = 0; i < (this.Review).length; i++){
          if(this.Review[i].rate==1){
       sum1 +=1;
        }
         if(this.Review[i].rate==2){
         sum2 +=1;
        } 
        if(this.Review[i].rate==3){
       sum3 +=1;
        }
         if(this.Review[i].rate==4){
         sum4 +=1;
        }
         if(this.Review[i].rate==5){
        sum5 +=1;
        }
      }
 return sum1,sum2,sum3,sum4,sum5;
 
   }
},
  methods: {
    
    logOutNow() {
      //alert(5);
      localStorage.clear();
      this.$router.push("/login");
    },
     FormReset() { //for restore form
                this.questionform.reset();
               },
               OfferForm(){
                
                 axios.post(`/offersetting`)
                 .then(response => {
                        this.OfferSetting=response.data.offerinfo;
                         this.District=response.data.district;
                                  });
               },
      OfferApply(){
this.offerform.post("/offerapply")
        //console.log('ok');
         .then(({ response}) => {
           toastr.warning('Register Successfully Submit');  
           $('#Offerapply').modal('toggle');
            this.FormReset();    
            })
            .catch(function(response) {
              toastr.warning("Sorry Try Agin");
        });
      },
      
    AddQuestion(){
       if(this.authenticatedname==null){
           alert('Please Login First');
       }
       else{
 this.$eventBus.$emit("loadingStatus", true);
this.questionform.post("user/question")
        //console.log('ok');
         .then(({ response}) => {
          this.$eventBus.$emit("loadingStatus", false);
          this.questionform.question='';
          [toastr.success("Your Question submit succefully")];
            axios.get(`/product/${this.$route.params.id}`)
              .then(response => {
                        this.headful.description=response.data.productdetails.metadescription;
                    this.headful.keywords=response.data.productdetails.keyword;
                     this.questionform.id = response.data.productdetails.id;
                    this.allProductdetails = response.data.productdetails;
                     this.Review = response.data.productdetails.userreview;
                  });

                    })
      
        .catch(function(response) {
          toastr.warning("Sorry Try Agin");
        });

       }
    }
   
  }
};
</script>


  


